	    <pre class="annotation_ru none" style="text-align: left; opacity:0; padding-top: 0px;">Группа Кюс образована в 2012 году, базируется в Краснодаре. Основные направления – <a  target="_blank" href="http://instructions.stranno.su/#Bookmark911">уличная деятельность</a>, <a href="http://stranno.su/#/neosuprematism">неосупрематизм</a>, <a href="http://stranno.su/#/screen_painting">живопись экрана</a>. Название «Кюс» — это не более, чем бессмысленное и вычурное сочетание букв. Изредка участвуем в выставках. Последними проектами были постеры <a href="http://stranno.su/#/strange_posters">«Странно»</a>, серия <a href="http://stranno.su/#/simulation_of_welfare">«Симуляция благополучия»</a> и <a href="http://stranno.su/#/threads">«Нити»</a>, а также самый большой <a href="http://stranno.su/#/screen_painting">курсор</a> в мире, нарисованный на крыше местного ТРЦ.

Учредители культурного центра <a href="http://stranno.su/#/the_mansion_of_culture">«Особняк Культуры»</a> (полное название — Автономная некоммерческая организация в сфере культуры, творчества и искусства "Краснодарский краевой художественно-исследовательский научно-просветительский центр «Особняк Культуры») и галереи <a href="http://stranno.su/#/the_high_of_culture">«Вершина Культуры»</a>. На данный момент создание таких институций-интервенций превращается в серию, которую нам ещё предстоит отрефлексировать.

Чтобы понять нашу методологию, рекомендуем прочитать <a href="http://instructions.stranno.su/">Инструкции</a>.

Для расширения собственного восприятия города, воспользуйтесь нашим <a href="http://derive.stranno.su">веб-приложением</a> для психогеографических исследований.

